import {
  MDBNavbar,
  MDBNavbarBrand,
  MDBNavbarNav,
  MDBNavbarToggler,
  MDBCollapse,
  MDBNavItem,
  MDBNavLink
} from "mdbreact";
import { Link } from "react-router-dom";
import React from "react";
import Logo from "../logo.png";
import UserBar from "./UserBar.jsx";

class AppHeader extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      collapse: false
    };
    this.onClick = this.onClick.bind(this);
    this.NavLinkonClick = this.NavLinkonClick.bind(this);
  }

  onClick() {
    this.setState({
      collapse: !this.state.collapse
    });
  }

  NavLinkonClick() {
    this.setState({
      collapse: false
    });
  }

  render() {
    const NavbarStyle = { backgroundColor: "#FFFFFF" };
    return (
      <header>
        teste
        <MDBNavbar style={NavbarStyle} light expand="md" scrolling fixed="top">
          <MDBNavbarBrand>
            <Link to="/">
              <img src={Logo} className="img-fluid Logo" alt="Folia Bahia" />
            </Link>
          </MDBNavbarBrand>
          <MDBNavbarToggler onClick={this.onClick} />
          <MDBCollapse isOpen={this.state.collapse} navbar>
            <MDBNavbarNav left>
              <MDBNavItem>
                <MDBNavLink to="/compre-aqui" onClick={this.NavLinkonClick}>
                  Compre Aqui
                </MDBNavLink>
              </MDBNavItem>
              <MDBNavItem>
                <MDBNavLink to="/camarotes" onClick={this.NavLinkonClick}>
                  Camarotes
                </MDBNavLink>
              </MDBNavItem>
              <MDBNavItem>
                <MDBNavLink to="/blocos" onClick={this.NavLinkonClick}>
                  Blocos
                </MDBNavLink>
              </MDBNavItem>
            </MDBNavbarNav>
          </MDBCollapse>
          <UserBar />
        </MDBNavbar>
      </header>
    );
  }
}

export default AppHeader;
