import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import React, { Suspense, lazy, useEffect } from "react";
import { useDispatch } from "react-redux";

import "./App.css";
import AppHeader from "./components/AppHeader.jsx";
import { MDBContainer, MDBSpinner } from "mdbreact";
import fetchProdutos from "./actions/fetchProdutos";

const Home = lazy(() => import("./components/home"));
const CompreAqui = lazy(() => import("./components/compreaqui"));
const Camarotes = lazy(() => import("./components/camarotes"));
const Blocos = lazy(() => import("./components/blocos"));
//const Navios = lazy(() => import("./components/navios"));
//const Hoteis = lazy(() => import("./components/hoteis"));
//const Atendimento = lazy(() => import("./components/atendimento"));
const MinhaConta = lazy(() => import("./components/minhaconta"));
const Carrinho = lazy(() => import("./components/carrinho"));

function App() {
  const containerStyle = { maxWidth: "600px" };
  const dispatch = useDispatch();

  useEffect(() => {
    fetchProdutos(dispatch);
  });

  return (
    <div className="App">
      <Router>
        <MDBContainer style={containerStyle} className="mb-2">
          <AppHeader />
          <Suspense fallback={<MDBSpinner yellow className="align-middle" />}>
            <Switch>
              <Route exact path="/" component={Home} />
              <Route path="/compre-aqui" component={CompreAqui} />
              <Route path="/camarotes" component={Camarotes} />
              <Route path="/blocos" component={Blocos} />
              <Route path="/minha-conta" component={MinhaConta} />
              <Route path="/carrinho" component={Carrinho} />
            </Switch>
          </Suspense>
        </MDBContainer>
      </Router>
    </div>
  );
}

export default App;
